<template><section>
<h1>CysForm 表单</h1>
<h2 id="%E5%9F%BA%E7%A1%80%E7%94%A8%E6%B3%95">基础用法 <a class="cys-markdown-permalink" href="#%E5%9F%BA%E7%A1%80%E7%94%A8%E6%B3%95">#</a></h2>
<demo-block class="demo-box">
                            <div class="source" slot="source"><template>
  <div>
    <cys-form ref="form" v-model="formData" :label-width="'80px'" :inline="true" :rules="rules">
      <cys-form-item :label="'名称：'" prop="name" :label-width="'80px'">
        <cys-input v-model="formData.name" placeholder="请输入内容"></cys-input>
      </cys-form-item>
      <cys-form-item :label="'邮箱：'" prop="mail">
        <cys-input v-model="formData.mail" placeholder="请输入内容"></cys-input>
      </cys-form-item>
      <cys-form-item :label="'邮箱11：'" prop="select">
        <cys-select v-model="formData.select" :filter="true" :clearable="true" placeholder="请选择">
          <cys-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
          </cys-option>
        </cys-select>
      </cys-form-item>
      <cys-form-item :label="'邮箱11：'" prop="date">
        <cys-date-picker v-model="formData.date" placeholder="选择日期"></cys-date-picker>
      </cys-form-item>
      
    </cys-form>
    <cys-button @click="handleSubmit">提交</cys-button>
    <cys-button plain @click="handleReset">重置</cys-button>
  </div>
</template>

</div>
                            <p>通过<code>plain</code>属性可以设置为朴素的按钮</p>

